<template>
    <div>
        <el-row>
            <el-col :span="1">年份</el-col>
            <el-col :span="5">
                <el-select placeholder="年份" v-model="year">
                    <el-option label="全部" value="all"></el-option>
                    <el-option label="2021" value="2021"></el-option>
                    <el-option label="2020" value="2020"></el-option>
                    <el-option label="2019" value="2019"></el-option>
                    <el-option label="2018" value="2018"></el-option>
                </el-select>
            </el-col>
            <el-col :span="1" :offset="3">月份</el-col>
            <el-col :span="5">
                <el-select placeholder="月份" v-model="month" :disabled="isAll">
                    <el-option label="全部" value="all"></el-option>
                    <el-option label="1" value="1"></el-option>
                    <el-option label="2" value="2"></el-option>
                    <el-option label="3" value="3"></el-option>
                    <el-option label="4" value="4"></el-option>
                </el-select>
            </el-col>
        </el-row>
        <el-row style="margin-top:30px">
            <el-table :data="tableData">
                <el-table-column label="排名" type="index" :index="defaultIndex"></el-table-column>
                <el-table-column label="排名（手）" prop="a"></el-table-column>
                <el-table-column label="姓名" prop="b"></el-table-column>
                <el-table-column label="部门"   :filters="[{ text: '家', value: '家' }, { text: '公司', value: '公司' }]"
                    :filter-method="filterBumen" prop="c">
                </el-table-column>
                <!-- <el-table-column label="年份" :filters="[{ text: '2021', value: '2021' }, { text: '2020', value: '2020' }
                , { text: '2019', value: '2019' }, { text: '2018', value: '2018' }]"
                    :filter-method="filterYear" prop="e">
                </el-table-column>
                <el-table-column label="月份" :filters="[{ text: '1', value: '1' }, { text: '2', value: '2' }, { text: '3', value: '3' }
                , { text: '4', value: '4' }, { text: '5', value: '5' }, { text: '6', value: '6' }, { text: '7', value: '7' }
                , { text: '8', value: '8' }, { text: '9', value: '9' }, { text: '10', value: '10' }, { text: '11', value: '11' }
                , { text: '12', value: '12' }]"
                    :filter-method="filterMonth" prop="d">
                </el-table-column>
                 -->
                <el-table-column label="总积分" prop="f"></el-table-column>
            </el-table>
        </el-row>
        <el-row>
            <div id="footer">
                <el-pagination
                    background
                    :page-size="4"
                    :pager-count="7"
                    :total="100"
                    @current-change="handleCurrentChange"
                    layout="total,prev, pager, next,jumper">
                </el-pagination>
            </div>
        </el-row>
    </div>
</template>
<script>
export default {
    data(){
        return{
            page:1,
            year:'all',
            month:'all',
            isAll:false,
            result:'',
            tableData:[
                {
                    a:'1',b:'2',c:'2',d:'2',e:'2021',f:'222'
                },{
                    a:'1',b:'2',c:'2',d:'1',e:'2020',f:'222'
                },{
                    a:'1',b:'2',c:'2',d:'2',e:'2020',f:'222'
                },{
                    a:'1',b:'2',c:'2',d:'3',e:'2019',f:'222'
                },{
                    a:'1',b:'2',c:'2',d:'1',e:'2018',f:'222'
                },{
                    a:'1',b:'2',c:'2',d:'2',e:'2020',f:'222'
                },{
                    a:'1',b:'2',c:'2',d:'5',e:'2021',f:'222'
                },{
                    a:'1',b:'2',c:'2',d:'6',e:'2021',f:'222'
                }
            ],
        }
    },
    watch:{
        year:{
            handler(){
                console.log(this.year)
                if(this.year=='all'){
                    console.log("关闭月")
                    this.isAll =true
                }else{
                    console.log("开启月")
                    this.isAll=false
                }
            },
            immediate:true
        },
        
    },
    methods:{
        // value ——选中的筛选值，row每一行
        filterMonth(value, row) {
            return row.d === value;
        },
        filterBumen(value,row){
            return row.c ===value
        },
        filterYear(value, row) {
            return row.e === value;
        },
        defaultIndex(index){
            return index+1
        },
        handleCurrentChange(val){
            this.page=val
            console.log(typeof(this.page)+":"+this.page)
        },
    }
}
</script>
<style scoped>
#footer{
position:fixed;
left:20%;
bottom:10px;
height:30px;
width:100%;
}
</style>


